import React, { useState } from 'react';
import Head from 'next/head';

import data from '../data';

import calculatorService from '../services/calculatorService';

const Home = () => {
  const [calculations, setCalculations] = useState([]);

  return (
    <div>
      <Head>
        <title>Shares Saver Calculator</title>
        <link rel="icon" href="/favicon.ico" />
      </Head>

      <div className="container">
        <h1 className="title">Shares Saver Calculator</h1>
        <p className="description">
          Choose a saver plan and enter your monthly saving contribution.
        </p>
        <div className="details"><b>Name: </b><span>{data.asx.name}</span></div>
        <div className="details"><b>Entry Fee: </b><span>{data.asx.entryFee}%</span></div>
        <div className="details"><b>Management Fee: </b><span>{data.asx.totalExpenseRatio}% p.a</span></div>
        <button onClick={() => setCalculations(calculatorService.calculate(data.asx, 100))}>
          Calculate
        </button>
        { calculations.length > 0 &&
          <table>
            <tr>
              <th>Date</th>
              <th>Type</th>
              <th>Shares Change</th>
              <th>Total Shares</th>
              <th>Total Deposited</th>
              <th>Worth</th>
              <th>Growth</th>
            </tr>
            { calculations.map(calculation =>
            <tr>
              <td>{calculation.date.toISOString().substr(0, 10)}</td>
              <td>{calculation.type}</td>
              <td>{calculation.shareChange.toFixed(2)}</td>
              <td>{calculation.totalShares.toFixed(2)}</td>
              <td>{calculation.totalDeposited.toFixed(2)} euros</td>
              <td>{calculation.worth.toFixed(2)} euros</td>
              <td className={calculation.growth < 0 ? 'negative' : 'positive'}>{calculation.growth.toFixed(2)}%</td>
            </tr>
          )}
          </table>
        }
      </div>
      <style jsx>{`
        .container {
          align-items: center;
          display: flex;
          flex-direction: column;
          width: 900px;
          color: #333;
        }
        .title {
          margin: 0;
          width: 100%;
          padding-top: 80px;
          line-height: 1.15;
          font-size: 48px;
        }
        .title,
        .description {
          text-align: center;
        }
        .details {

        }
        tr:nth-child(even) {background: #CCC}
        tr:nth-child(odd) {background: #FFF}
        td {
          padding: 10px;
        }
      `}</style>
    </div>
  );
}

export default Home;
